'use client';
import { useEffect, useState } from 'react';
import axios from 'axios';

const AdminGroupPage = () => {
  const [groupName, setGroupName] = useState('');
  const [users, setUsers] = useState([]);
  const [selectedUsers, setSelectedUsers] = useState([]);

  const token = typeof window !== 'undefined' ? localStorage.getItem('token') : '';

  useEffect(() => {
    // Fetch all users
    axios
      .get('http://localhost:4000/api/group/users', {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
      .then((res) => setUsers(res.data.users))
      .catch((err) => console.error(err));
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();

    axios
      .post(
        'http://localhost:4000/api/group/create',
        {
          name: groupName,
          userIds: selectedUsers
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        }
      )
      .then(() => {
        alert('Group created!');
        setGroupName('');
        setSelectedUsers([]);
      })
      .catch((err) => {
        console.error(err);
        alert('Group creation failed');
      });
  };

  const handleUserSelect = (userId) => {
    setSelectedUsers((prev) =>
      prev.includes(userId) ? prev.filter((id) => id !== userId) : [...prev, userId]
    );
  };

  return (
    <div className="p-4">
      <h2 className="text-2xl font-bold mb-4">ðŸ‘¥ Create Group</h2>

      <form onSubmit={handleSubmit} className="space-y-4 bg-white p-4 rounded shadow max-w-lg">
        <input
          type="text"
          placeholder="Group Name"
          value={groupName}
          onChange={(e) => setGroupName(e.target.value)}
          className="w-full border px-3 py-2 rounded"
          required
        />

        <div>
          <h3 className="font-semibold mb-2">Select Users:</h3>
          <div className="space-y-1 max-h-40 overflow-y-auto border p-2 rounded">
            {users.map((user) => (
              <label key={user._id} className="block">
                <input
                  type="checkbox"
                  value={user._id}
                  checked={selectedUsers.includes(user._id)}
                  onChange={() => handleUserSelect(user._id)}
                  className="mr-2"
                />
                {user.name} ({user.email})
              </label>
            ))}
          </div>
        </div>

        <button className="bg-blue-600 text-white px-4 py-2 rounded">Create Group</button>
      </form>
    </div>
  );
};

export default AdminGroupPage;
